import React, { useState, useEffect, useRef } from "react";
import "../../../map.css";
import useOutsideClick from "../useOutsideClick";

const Donor = ({
  donors,
  selectedDonors,
  setSelectedDonors,
  selectedLanguage,
}) => {
  const [show, setSate] = useState(false);
  const ref = useRef();

  useOutsideClick(ref, () => {
    if (show) setSate(false);
  });

  const handleOptionChange1 = (i) => {
    if (selectedDonors.some((c) => c === i)) {
      let arr = selectedDonors.filter((x) => x !== i);
      selectedDonors = arr;
    } else {
      selectedDonors.push(i);
    }
    setSelectedDonors([...selectedDonors]);
  };

  return (
    <div className="community">
      <button
        className="btnSupport btn_status"
        id="btnSelect"
        onClick={() => setSate(!show)}
      >
        <label className="support">
          {selectedLanguage == "arm" ? "Դոնոր" : "Դոնոր"}
        </label>
        <img
          src={require("../../../icon/arrow.svg").default}
          onClick={() => setSate(!show)}
        />
      </button>
      {show && (
        <div ref={ref} className="NestedStatus">
          <div className="statuses">
            {donors.map((i) => (
              <li
                className="li1"
                key={i.id}
                onClick={() => handleOptionChange1(i.id)}
                style={{
                  backgroundColor: selectedDonors.some((item) => item === i.id)
                    ? "#A4C2D8"
                    : "#FAFAFA",
                }}
              >
                {i.name}
              </li>
            ))}
          </div>
        </div>
      )}
    </div>
  );
};

export default Donor;
