import React, { useEffect, useState, useRef } from "react";
import "../../../map.css";
import useOutsideClick from "../useOutsideClick";

const Regions = ({
  regions,
  setRegions,
  selectedCities,
  setSelectedCities,
  selectedLanguage,
}) => {
  const [show, setSate] = useState(false);
  const [openRegion, setOpenRegion] = useState([]);
  const ref = useRef();
  const [search, setSearch] = useState("");
  const [result, setResult] = useState([]);

  useOutsideClick(ref, () => {
    if (show) setSate(false);
  });

  useEffect(() => {
    let results = regions.map((region) => {
      let arr = region.cities.filter((city) =>
        city.name?.toLowerCase().includes(search.toLowerCase())
      );
      return { ...region, cities: arr };
    });
    setResult(results);
  }, [search, regions]);

  const selectCity = (id) => {
    const arr = selectedCities.filter((item) => item != id);
    if (arr.length == selectedCities.length) {
      selectedCities.push(id);
      setSelectedCities([...selectedCities]);
    } else {
      setSelectedCities([...arr]);
    }
  };

  const checkRegion = (e, index) => {
    const checked = e.target.checked;
    const items = regions[index].cities;
    if (checked) {
      for (let i = 0; i < items.length; i++) {
        const exists = selectedCities.some((item) => item == items[i].id);
        if (!exists) {
          selectedCities.push(items[i].id);
        }
      }

      setSelectedCities([...selectedCities]);
    } else {
      let n = selectedCities;
      for (let i = 0; i < items.length; i++) {
        const ind = n.findIndex((item) => item == items[i].id);
        if (ind >= 0) {
          n.splice(ind, 1);
        }
      }
      setSelectedCities([...n]);
    }
    regions[index].active = !regions[index].active;
    setRegions([...regions]);
  };

  const openRegions = (id) => {
    if (openRegion.some((item) => item === id)) {
      let index = openRegion.findIndex((item) => item === id);
      openRegion.splice(index, 1);
      setOpenRegion([...openRegion]);
    } else {
      openRegion.push(id);
      setOpenRegion([...openRegion]);
    }
  };

  return (
    <div className="community">
      <button
        className="btnSupport btn_support"
        id="btnSelect"
        onClick={() => setSate(!show)}
      >
        <label className="support">
          {selectedCities.length > 0
            ? selectedLanguage == "arm"
              ? `Ընտրված է ${selectedCities.length} համայնք`
              : `${selectedCities.length} communities is selected`
            : selectedLanguage == "arm"
            ? `Համայնք`
            : `Community`}
        </label>
        <img
          src={require("../../../icon/arrow.svg").default}
          onClick={() => setSate(!show)}
        />
      </button>
      {show && (
        <div ref={ref} className="nested">
          <button className="city_search support_search">
            <input
              className="input_search_city input_search_support"
              type="text"
              value={search}
              placeholder={selectedLanguage == "arm" ? "Փնտրել" : "Search"}
              onChange={(e) => setSearch(e.target.value)}
            />
            <img
              onClick={() => setSearch("")}
              className="img_close_support"
              src={require("../../../icon/closing icon.svg").default}
            />
            <img
              className="img_search_city img_search_support"
              src={require("../../../icon/search icon.svg").default}
            />
          </button>
          {result.map((region, index) => (
            <div key={region.regionId} className="list3">
              <ul className="ul3">
                <div className="supportList">
                  <input
                    type="checkbox"
                    id="check"
                    checked={region.active}
                    className="checkbox1"
                    onChange={(e) => checkRegion(e, index)}
                  />
                </div>
                <label
                  className="category_name"
                  onClick={(e) => openRegions(region.regionId)}
                >
                  {region.regionName} ({region.cities.length})
                </label>
                <img
                  className="arrowSelect"
                  src={require("../../../icon/arrow.svg").default}
                  onClick={(e) => openRegions(region.regionId)}
                />
                {openRegion.some((item) => item === region.regionId) ? (
                  <div className="support_types">
                    {region.cities.map((city, index) => (
                      <li
                        key={city.id}
                        style={{
                          backgroundColor: selectedCities.some(
                            (item) => item === city.id
                          )
                            ? "#A4C2D8"
                            : "#FAFAFA",
                        }}
                        className="li3"
                        onClick={() => selectCity(city.id)}
                      >
                        {city.name}
                      </li>
                    ))}
                  </div>
                ) : null}
              </ul>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default Regions;
