import React, { useState, useEffect } from "react";
import "./map.css";
import Regions from "./componenets/Filter/Regions/Regions";
import Language from "./componenets/Filter/Language/Language";
import Status from "./componenets/Filter/Status/Status";
import Support from "./componenets/Filter/Support/Support";
import MapPageFunc from "./componenets/Map/MapPageFunc";
import Organization from "./componenets/Filter/Organization/Organization";
import Donor from "./componenets/Filter/Donor/Donor";

const Select = ({ setInfo }) => {
  const [donors, setDonors] = useState([]);
  const [selectedDonors, setSelectedDonors] = useState([]);
  const [regions, setRegions] = useState([]);
  const [selectedCities, setSelectedCities] = useState([]);
  const [coordinates, setCoordinates] = useState([]);
  const [statuses, setStatuses] = useState([]);
  const [selectedStatuses, setSelectedStatuses] = useState([]);
  const [supports, setSupports] = useState([]);
  const [selectedSupports, setSelectedSupports] = useState([]);
  const [organizations, setOrganizations] = useState([]);
  const [selectedOrganizations, setSelectedOrganizations] = useState([]);
  const [selectedLanguage, setSelectedLanguage] = useState(
    localStorage.getItem("lang")
  );

  useEffect(() => {
    const lang = localStorage.getItem("lang");
    if (lang) {
      setSelectedLanguage(lang);
    } else {
      localStorage.setItem("lang", "arm");
    }
    if (window.innerWidth < 360) {
      setToggle(false);
    } else {
      setToggle(true);
    }
    window.addEventListener("resize", () => {
      if (window.innerWidth >= 360) {
        setToggle(true);
      }
    });
  }, []);

  useEffect(() => {
    fetch("/api/filterEng", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        communityid: selectedCities,
        statusid: selectedStatuses,
        supportid: selectedSupports,
        organizationid: selectedOrganizations,
        donorid: selectedDonors,
      }),
    })
      .then((res) => {
        return res.json();
      })
      .then((res) => {
        setCoordinates(res.data);
      })
      .catch((e) => {});
  }, [
    selectedCities,
    selectedStatuses,
    selectedSupports,
    selectedOrganizations,
    selectedDonors,
  ]);

  useEffect(() => {
    fetch("/api/donors", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ language: "arm" }),
    })
      .then((res) => res.json())
      .then((data) => {
        setDonors(data.data);
      })
      .catch((err) => {});

    fetch("/api/communities", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        language: selectedLanguage,
      }),
    })
      .then((res) => {
        return res.json();
      })
      .then((res) => {
        setRegions(res.data);
      });

    fetch("/api/statuses", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        language: selectedLanguage,
      }),
    })
      .then((res) => {
        return res.json();
      })
      .then((res) => {
        setStatuses(res.data);
      })
      .catch((err) => {
        console.log(err);
      });

    fetch("/api/organizations", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        language: selectedLanguage,
      }),
    })
      .then((res) => {
        return res.json();
      })
      .then((res) => {
        setOrganizations(res.data);
      })
      .catch();

    fetch("/api/supportsList", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        language: selectedLanguage,
      }),
    })
      .then((res) => {
        return res.json();
      })
      .then((res) => {
        let a = res.data;
        a.forEach((el) => {
          el.active = false;
        });
        setSupports(a);
      })
      .catch((err) => {});
  }, [selectedLanguage]);

  const [toggle, setToggle] = useState(false);
  const handleClick = () => {
    setToggle(!toggle);
  };

  return (
    <div>
      <div className="general">
        <div className="filters">
          <button
            onClick={handleClick}
            className={
              !toggle
                ? "hamburger-btn hamburger-button"
                : "hamburger-btn-open hamburger-button"
            }
            type="button"
          >
            <span
              className={!toggle ? "hamburger-inner" : "hamburger-x"}
            ></span>
          </button>
          <nav className="menu">
            {toggle && (
              <ul className="menu-list">
                <Regions
                  selectedCities={selectedCities}
                  setSelectedCities={setSelectedCities}
                  setRegions={setRegions}
                  regions={regions}
                  selectedLanguage={selectedLanguage}
                />
                <Support
                  selectedSupports={selectedSupports}
                  setSelectedSupports={setSelectedSupports}
                  supports={supports}
                  setSupports={setSupports}
                  selectedLanguage={selectedLanguage}
                />
                <Status
                  selectedStatuses={selectedStatuses}
                  setSelectedStatuses={setSelectedStatuses}
                  statuses={statuses}
                  selectedLanguage={selectedLanguage}
                />
                <Donor
                  selectedDonors={selectedDonors}
                  setSelectedDonors={setSelectedDonors}
                  donors={donors}
                  selectedLanguage={selectedLanguage}
                />
                <Organization
                  selectedOrganizations={selectedOrganizations}
                  setSelectedOrganizations={setSelectedOrganizations}
                  organizations={organizations}
                  selectedLanguage={selectedLanguage}
                />
                <Language
                  selectedLanguage={selectedLanguage}
                  setSelectedLanguage={setSelectedLanguage}
                />
              </ul>
            )}
          </nav>
        </div>

        <MapPageFunc
          selectedSupports={selectedSupports}
          selectedStatuses={selectedStatuses}
          selectedCities={selectedCities}
          coordinates={coordinates}
          selectedDonors={selectedDonors}
          setInfo={setInfo}
          selectedLanguage={selectedLanguage}
          selectedOrganizations={selectedOrganizations}
        />
      </div>
    </div>
  );
};

export default Select;
