import React from "react";
import "./footer.css";

function Footer() {
  return (
    <div className="footer">
      <div className="footer_top">
        <div className="footer_top_title">
          <p>Գործընկեր կազմակերպություններ</p>
        </div>
        <div className="footer_component_organizations">
          <div className="blazon">
            <img
              className="blazon1"
              src={require("../../icon/gov logo.png").default}
            />
          </div>
          <div>
            <img
              className="blazon_organization"
              src={require("../../icon/swiss.png").default}
            />
          </div>
          <div>
            <img
              className="blazon2"
              src={require("../../icon/pahapan.png").default}
            />
          </div>
        </div>
      </div>

      <hr className="footer_line"></hr>
      <div className="footer_copyright">Copyright information</div>
      <div className="footer_bottom">
        <div className="footer_bottom_comp">
          <img
            className="dwave_icon"
            src={require("../../icon/dwave.png").default}
          />
          <div className="footer_bottom_org">
            Designed and developed by{" "}
            <a className="href" href="https://d-wave.tech/">
              {" "}
              Digital Wave
            </a>
          </div>
        </div>
        <div></div>
      </div>
    </div>
  );
}

export default Footer;
