import React, { useEffect, useState } from "react";
import axios from "axios";
import ProgramDetails from "./ProgramDetails";

function InformationSidebar({
  mappointid,
  setOpensidebar,
  selectedLanguage,
  selectedSupports,
  selectedOrganizations,
  selectedStatuses,
  selectedCities,
  selectedDonors,
}) {
  const [data, setData] = useState([]);

  useEffect(() => {
    setData([]);
    axios
      .post(`/api/programListForFilterEng`, {
        mappointid,
        statusid: selectedStatuses,
        supportid: selectedSupports,
        organizationid: selectedOrganizations,
        language: selectedLanguage,
        donorid: selectedDonors,
      })
      .then((response) => {
        setData(response.data.data);
      })
      .catch((e) => {
        console.log("error");
      });
  }, [
    mappointid,
    selectedLanguage,
    selectedStatuses,
    selectedSupports,
    selectedOrganizations,
    selectedCities,
    selectedDonors,
  ]);

  return (
    <ProgramDetails
      data={data}
      mappointid={mappointid}
      setOpensidebar={setOpensidebar}
      selectedStatuses={selectedStatuses}
      selectedSupports={selectedSupports}
      selectedOrganizations={selectedOrganizations}
      selectedCities={selectedCities}
      selectedDonors={selectedDonors}
    />
  );
}

export default InformationSidebar;
