import React, { useState } from "react";

function ProgramDetails({ data, setOpensidebar }) {
  const [show, setShow] = useState(false);
  let [category_arr, setCategory_arr] = useState([]);

  const closeSidebar = () => {
    setOpensidebar(false);
  };

  const openProg = (id) => {
    if (id === show) {
      setShow(false);
    } else {
      setShow(id);
    }
    data.map((prog) => {
      if (prog.id === id) {
        const arr = [];

        prog.category_support.map((item) => {
          if (!arr.some((i) => i.categoryid === item.categoryid))
            arr.push(item.category);
        });
        let set = new Set(arr);
        setCategory_arr([...Array.from(set)]);
      }
    });
  };

  return (
    <div>
      <div className="infoSidebar">
        <div className="info_sidebar_container">
          <div>
            <button className="btn_close">
              <img
                className="org_icon"
                onClick={closeSidebar}
                src={require("../../../src/icon/closing icon.svg").default}
              />
            </button>

            <img
              className="marzpetaran_icon"
              src={require("../../../src/icon/marzpetaran.jpg").default}
            />
          </div>
          <div style={{ marginTop: "40px" }}>
            {data.length > 0
              ? data.map((element, index) => {
                  return (
                    <div className="info_detail" key={element.id}>
                      <div key={index}>
                        <div
                          className="title_row"
                          onClick={(e) => {
                            openProg(element.id);
                          }}
                        >
                          <img
                            className={
                              show && show === element.id
                                ? "arrow_select_close"
                                : "arrow_select"
                            }
                            src={require("../../../src/icon/arrow.svg").default}
                          />
                          <div
                            style={{
                              width: "254px",
                              lineHeight: "27px",
                              paddingLeft: "16px",
                            }}
                          >
                            {element.name}
                          </div>
                        </div>

                        {show === element.id && (
                          <div className="supports_types">
                            <div style={{ paddingBottom: "32px" }}>
                              <div className="info_row">
                                <img
                                  className="det_icon"
                                  src={
                                    require("../../../src/icon/time.svg")
                                      .default
                                  }
                                />
                                <p className="paragraph p">
                                  {element.start_date}-{element.end_date}
                                </p>
                              </div>
                              <div className="info_row">
                                <img
                                  className="det_icon"
                                  src={
                                    require("../../../src/icon/community.svg")
                                      .default
                                  }
                                />
                                <p className="paragraph p">
                                  {element.community}
                                </p>
                              </div>
                              <div className="info_row">
                                <img
                                  className="det_icon"
                                  src={
                                    require("../../../src/icon/budget.svg")
                                      .default
                                  }
                                />
                                <p className="paragraph p">{element.budget}</p>
                              </div>

                              {/* category */}
                              <div className="info_row">
                                <img
                                  className="det_icon"
                                  src={
                                    require("../../../src/icon/sphere.svg")
                                      .default
                                  }
                                />
                                <div>
                                  {category_arr.map((el, index) => {
                                    return (
                                      <p className="paragraph p" key={index}>
                                        {""}
                                        {el}
                                        {", "}
                                      </p>
                                    );
                                  })}
                                </div>
                              </div>

                              {/* support */}
                              <div className="info_row">
                                <img
                                  className="det_icon"
                                  src={
                                    require("../../../src/icon/support.svg")
                                      .default
                                  }
                                />
                                <div>
                                  {element.category_support.map((el, index) => {
                                    return (
                                      <p className="paragraph p" key={index}>
                                        {" "}
                                        {el.support}
                                        {", "}
                                      </p>
                                    );
                                  })}
                                </div>
                              </div>

                              {element.organization.map((el, index) => {
                                return (
                                  <div className="info_row" key={index}>
                                    <img
                                      className="det_icon"
                                      src={
                                        require("../../../src/icon/organization.svg")
                                          .default
                                      }
                                    />
                                    <p className="paragraph p">{el}</p>
                                  </div>
                                );
                              })}
                              <div className="info_row">
                                <img
                                  className="det_icon"
                                  src={
                                    require("../../../src/icon/head name.svg")
                                      .default
                                  }
                                />
                                <p className="paragraph p">{element.manager}</p>
                              </div>
                              <div className="info_row">
                                <img
                                  className="det_icon"
                                  src={
                                    require("../../../src/icon/contact.svg")
                                      .default
                                  }
                                />
                                <p className="paragraph p">
                                  {element.contactPerson}
                                </p>
                              </div>
                              <div className="info_row">
                                <img
                                  className="det_icon"
                                  src={
                                    require("../../../src/icon/ongoing.svg")
                                      .default
                                  }
                                />
                                <p className="paragraph p">{element.status}</p>
                              </div>
                              <div className="info_row">
                                <img
                                  className="det_icon"
                                  src={
                                    require("../../../src/icon/description.svg")
                                      .default
                                  }
                                />
                                <p className="paragraph p">
                                  {element.description}
                                </p>
                              </div>
                              {element.donorName ? (
                                <div className="info_row">
                                  <img
                                    className="det_icon"
                                    src={
                                      require("../../../src/icon/organization.svg")
                                        .default
                                    }
                                  />
                                  <p className="paragraph p">
                                    {element.donorName}
                                  </p>
                                </div>
                              ) : null}
                            </div>
                          </div>
                        )}
                      </div>
                    </div>
                  );
                })
              : null}
          </div>
        </div>
      </div>
    </div>
  );
}

export default ProgramDetails;
