import React, { useEffect, useState, useRef } from "react";
import "../../../map.css";
import useOutsideClick from "../useOutsideClick";

const Organizations = ({
  organizations,
  selectedOrganizations,
  setSelectedOrganizations,
  selectedLanguage,
}) => {
  const [show, setSate] = useState(false);
  const ref = useRef();
  const [search, setSearch] = useState("");
  const [result, setResult] = useState([]);

  useEffect(() => {
    const results = organizations.filter((organization) =>
      organization.name.toLowerCase().includes(search.toLowerCase())
    );
    setResult(results);
  }, [search, organizations]);

  useOutsideClick(ref, () => {
    if (show) setSate(false);
  });

  const handleOptionChange1 = (i) => {
    if (selectedOrganizations.some((c) => c === i)) {
      let arr = selectedOrganizations.filter((x) => x !== i);
      setSelectedOrganizations(arr);
    } else {
      setSelectedOrganizations([...selectedOrganizations, i]);
    }
  };

  return (
    <div className="community">
      <button
        className="btnSupport btn_organization"
        id="btnSelect"
        onClick={() => setSate(!show)}
      >
        <label className="support">
          {selectedOrganizations.length > 0
            ? selectedLanguage == "arm"
              ? `Ընտրված է ${selectedOrganizations.length} կազմակերպություն`
              : `${selectedOrganizations.length} organization is selected`
            : selectedLanguage == "arm"
            ? `Կազմակերպություն`
            : `Organization`}
        </label>
        <img
          src={require("../../../icon/arrow.svg").default}
          onClick={() => setSate(!show)}
        />
      </button>
      {show && (
        <div ref={ref} className="NestedSelect">
          <button className="city_search">
            <input
              className="input_search_city input_search_organization"
              type="text"
              placeholder={selectedLanguage == "arm" ? "Փնտրել" : "Search"}
              value={search}
              onChange={(e) => setSearch(e.target.value)}
            />
            <img
              onClick={() => setSearch("")}
              className="img_close_org"
              src={require("../../../icon/closing icon.svg").default}
            />
            <img
              className="img_search_city img_search_organization"
              src={require("../../../icon/search icon.svg").default}
            />
          </button>
          <div className="list organization">
            {result.map((i, index) => (
              <li
                className="li1"
                key={index}
                onClick={() => handleOptionChange1(i.id)}
                style={{
                  backgroundColor: selectedOrganizations.some(
                    (item) => item === i.id
                  )
                    ? "#A4C2D8"
                    : "#FAFAFA",
                }}
              >
                {i.name}
              </li>
            ))}
          </div>
        </div>
      )}
    </div>
  );
};

export default Organizations;
